.write{
    margin-top: 60px;
    padding-top: 50px;
}
.writeForm{
position: relative;
}
.writeFormGroup{
margin-left: 150px;
display: flex;
align-items: center;
}

.writeIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: gray;
    cursor: pointer;

}

.writeInput{
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
   
}

.writeInput{
    outline: none;
}

.writeText{
    font-size: 20px;
    height: 100vh;
}

.writeSubmit{
    position: absolute;
    top: 500px;
    right: 140px;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 30px;
}

.writeImg{
    margin-left: 150px;
    height:350px;
    width: 70vw;
    border-radius: 10px;
    object-fit: cover;
}