.settings{
    display: flex;
    margin-top: 60px;
}

.settingsWrapper{
    flex: 9;
    padding: 20px;
}

.settingsTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.settingsUpdateTitle{
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
    
}
.settingsDeleteTitle{
    color: lightcoral;
    font-size: 18px;
    cursor: pointer;
}

.settingsForm{
    display: flex;
    flex-direction: column;

}

.settingsPP{
    display: flex;
    align-items: center;
    margin: 10px 0;
}
.settingsPP >img{
    width: 100px;
    height: 100px;
    border-radius: 20px;
    object-fit: cover;
}

.settingsPPIcon{
    width: 30px;
    height: 30px;
    border-radius: 10%;
    background-color: lightcoral;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer;
}

.settingsForm > label{
    font-size: 20px;
    margin-top: 20px;
}
.settingsForm > input{
    color: grey;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 4px solid black;
    background-color: lightgrey;
    border-radius: 10px;
}

.settingsUpdate{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}