.sideBar{
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fbfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sideBarItem{
display: flex;
flex-direction: column;
align-items: center;
}
.sideBarTitle{
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sideBarItem > img {
    margin-top: 15px;
    height: 250px;
    width: 300px;
    border-radius: 2%;

}
.sideBarItem > p{
    padding: 30px;
}

.sideBarList{
    list-style: none;
    margin-bottom: 30px;
}
.sideBarListItem{
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor:pointer;
}
.sideBarSocial{
margin-top: 15px;
width: 250px;
display: flex;
align-items: center;
justify-content: center;
}

.sideBarIcon{
font-size: 25px;
margin-left: 15px;
cursor: pointer;
}